$main-color: #ff7300;
$secondary-color: #cf2158;

* {
  margin: 0;
  padding: 0;

  box-sizing: border-box;
  scroll-behavior: smooth;
  user-select: none;
}

html {
  overflow-x: hidden;
  background-color: #232323;
  #root {
    .App {
      width: 100vw;
      height: 100vh;

      font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
      .navbar {
        width: 100%;

        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #232323;
        position: fixed;
        top: 0;
        z-index: 1;
        .container {
          width: 50%;
          display: flex;
          justify-content: space-around;
          align-items: center;

          margin-top: 20px;
          padding-top: 10px;
          padding-bottom: 10px;

          border-bottom: 2px solid $secondary-color;
          a {
            font-size: 20px;
            font-weight: 600;
            line-height: 20px;
            text-align: center;

            color: rgb(151, 151, 151);
            text-decoration: none;
          }
          a:hover {
            color: $secondary-color;
            font-size: 24px;
            transition: all 0.1s, color 0.5s;
          }
        }
      }

      #intro {
        width: 100vw;
        max-width: 100vw;
        height: 100%;

        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;

        color: white;

        font-style: italic;
        font-size: 20px;

        .container {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          h3 {
            position: relative;
            left: -100px;
          }
          #name {
            font-weight: 700;
            font-size: 70px;
          }
          #title {
            color: rgb(151, 151, 151);
            font-style: normal;
          }
        }
      }
      .scroll {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        color: $main-color;

        position: relative;
        top: 150px;
        transition: top 1.5s ease;

        cursor: pointer;

        img {
          width: 10%;
          filter: hue-rotate(320deg) saturate(300%);
        }
      }

      .active {
        top: 250px;
      }
      #about {
        overflow-x: hidden;
        scroll-margin-top: 150px;

        color: white;
        display: grid;
        grid-template-columns: 20% 20%;
        column-gap: 20%;

        justify-content: center;

        row-gap: 50px;

        text-indent: 3%;

        h1 {
          font-size: 40px;
          grid-column-start: 1;
          grid-column-end: 3;
          text-align: center;
          padding-bottom: 50px;
        }
        span {
          color: $main-color;
          font-weight: bold;
        }
        .info {
          height: 100%;
          font-size: 20px;
          grid-column-start: 1;
          grid-column-end: 3;
        }

        .more-info {
          text-indent: 0;
          height: 100%;
          font-size: 18px;

          justify-self: self-end;
          h2 {
            color: $main-color;
          }
        }
        .contact {
          justify-self: self-start;

          font-size: 20px;
          text-indent: 0;
          user-select: text;

          h2 {
            color: $main-color;
          }
        }
      }
      #resume {
        scroll-margin-top: 150px;

        color: white;
        display: grid;
        grid-template-columns: 40% 40%;
        column-gap: 20%;
        justify-content: center;

        margin-top: 150px;
        margin-bottom: 50px;
        .color {
          color: $main-color;
        }

        h1 {
          width: 100%;
          font-size: 40px;
          grid-column-start: 1;
          grid-column-end: 3;
          text-align: center;
          padding-bottom: 50px;
        }

        .education,
        .experience,
        .other-strengths {
          width: 30%;
          justify-self: flex-end;
          font-weight: bold;
          color: rgb(236, 236, 236);

          span {
            color: rgb(173, 173, 173);
            font-weight: normal;
          }
        }
        .education,
        .experience {
          list-style-position: outside;
          list-style: disc;
          list-style-image: url(Components/Assets/circle.svg);
        }
        .bar {
          background-color: white;
          height: 10px;
          width: 35%;
          border-radius: 10px;
          margin-top: 10px;
        }
        .languages {
          font-weight: bold;
          .english-color {
            width: 80%;
          }
        }
        .text {
          width: 35%;
          display: flex;
          justify-content: space-between;
        }
        .inner-color {
          height: 100%;
          background-color: $secondary-color;
          border-radius: 10px;
        }

        .html-color {
          width: 90%;
        }
        .react-color {
          width: 80%;
        }
        .redux-color {
          width: 60%;
        }
        .web-design-color {
          width: 50%;
        }

        .cv {
          button {
            height: 50px;
            width: 200px;
            background-color: transparent;
            outline: none;
            border: 2px solid $secondary-color;

            color: rgb(223, 223, 223);

            font-size: 14px;
            font-weight: bold;

            transition: all 0.5s ease;
          }
          button:hover {
            cursor: pointer;
            background-color: $secondary-color;
          }
        }
      }
      #projects {
        scroll-margin-top: 260px;

        margin-top: 200px;
        margin-bottom: 300px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;

        span {
          font-size: 18px;

          position: relative;
          bottom: 70px;
          color: white;
          text-decoration: underline;
          text-decoration-color: $main-color;
          cursor: pointer;
          transition: all 0.2s ease;
        }
        span:hover {
          color: $main-color;
          font-size: 20px;
        }

        h1 {
          font-size: 40px;
        }
        .color {
          color: $main-color;
          padding-bottom: 50px;
        }

        .project {
          min-width: 100vw;
          display: flex;
          align-items: center;
          justify-content: space-evenly;

          .container {
            width: 32%;
            min-height: 300px;
            overflow-y: show;

            display: flex;
            justify-content: space-evenly;
            align-items: center;
            flex-direction: column;

            button {
              display: block;
              z-index: -1;
              background-color: #ff730060;
              border: 1px solid $main-color;
              color: white;
              outline: none;

              font-size: 20px;
              border-radius: 5px;

              width: 150px;
              height: 70px;
              cursor: pointer;

              backdrop-filter: blur(2px);
              box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.411);
            }
            button:hover {
              background-color: #ff7300e3;
            }

            background-color: rgb(255, 255, 255);
          }
          .youtube-clone {
            background-image: url('./Components/Assets/youtube.PNG');
            background-size: cover;
          }
          .amazon-clone {
            background-image: url('./Components/Assets/amazon.PNG');
            background-size: cover;
          }
          .a-star {
            background-image: url('./Components/Assets/astar.PNG');

            background-size: cover;
          }
        }
      }
      #contact {
        scroll-margin-top: 200px;
        display: flex;
        align-items: center;
        flex-direction: column;
        width: 100%;

        padding-bottom: 150px;

        h1 {
          font-size: 40px;
          color: white;
          span {
            color: $main-color;
          }
        }
        form {
          width: 35%;
          padding-top: 50px;

          input {
            width: 300px;
            height: 60px;
            padding-left: 10px;
            border-radius: 5px;
            background-color: #232323;

            border: 2px solid rgb(184, 184, 184);
            color: white;
            font-size: 13px;
          }

          input:hover {
            border: 2px solid $main-color;
            transition: all 0.2s ease;
          }
          input:focus,
          .message:focus {
            border: 2px solid $main-color;
            transition: all 0.2s ease;
            outline: none;
          }

          .container {
            color: rgb(218, 218, 218);
            display: flex;
            flex-direction: column;
            font-weight: bold;
            span {
              margin-bottom: 5px;
            }
          }

          .top {
            display: flex;
            justify-content: space-between;
          }

          #message {
            margin-top: 20px;
          }

          .message {
            color: white;
            background-color: #232323;
            border: 2px solid rgb(184, 184, 184);
            border-radius: 5px;
            padding-left: 5px;

            min-height: 250px;
            font-size: 15px;
            font-style: italic;
          }
          .message:hover {
            border: 2px solid $main-color;
            transition: all 0.2s ease;
          }

          #button {
            height: 60px;
            width: 50%;
            margin-left: 25%;

            margin-top: 50px;

            background-color: transparent;
            outline: none;
            border: 2px solid $secondary-color;

            color: rgb(223, 223, 223);

            font-size: 14px;
            font-weight: bold;

            transition: all 0.5s ease;
          }
          #button:hover {
            cursor: pointer;
            background-color: $secondary-color;
          }
        }
        #status {
          color: $main-color;
          font-style: italic;
        }
      }
    }
  }
}
